import Footer from "./../../FOOTER/Footer"
// import {Tabtitle} from "./../../Tabtitle"
import {SeoContext} from "./../../../Seocontext"


const Management = () => {
  useEffect(()=>{
    window.scrollTo({top:0, behavior:'smooth'})
  },[])
  // Tabtitle("Management - TRG : Raichand")
  return (
    <div>
      <SeoContext>
        <Header/>
        <Manage_breadcum/>
        <Manage_first_sec/>
        <Footer/>
        </SeoContext>
    </div>
  )
}

export default Management